









































export default {
	data() {
		return {
			myNc:'',
			myBz:'',
			uploadUrl:'../../../static/upload.png',
			windowHeight:''
		}
	},
	onShow() {
		this.getHeight();
		let val = uni.getStorageSync('content');
		if(val){
			this.myNc = val.myNc;
			this.myBz = val.myBz;
		}
	},
	onLoad(e) {
		this.myNc = e.nickName;
	},
	methods: {
		getHeight(){
			uni.getSystemInfo({
				success: (res) => {
					this.windowHeight = res.windowHeight;
				}
			})
		},
		goMine(){
			let arrs = {
				'myNc':this.myNc,
				'myBz':this.myBz
			}
			uni.setStorageSync('content',arrs); //参数同步保存到本地
			uni.navigateBack({
				delta:1
			})
		},
		uploadImg(){
			uni.chooseImage({
				count:1,
				sourceType:['album'],// 从相册选
				success: (res) => {
					if(res.tempFilePaths){
						this.uploadUrl = res.tempFilePaths;
					}
				}
			})
		}
	},
}



